import axios from '../configs/axios'
import Amplitude from 'amplitudejs'
import List from 'list.js'
import {accordion} from './accordion.js'

Amplitude.init({
  shuffle_on: false,
  songs: [
    {
      "name": "",//"Matt 1",
      "artist": "",//"English Standard Version",
      "album": "",//"ENGESV",
      "url": "",//"http://cloud.faithcomesbyhearing.com/mp3audiobibles2/ENGESVN2DA/B01___01_Matthew_____ENGESVN2DA.mp3",
      "cover_art_url": "",//"/assets/img/books/40_MAT.jpg",
      "playlist": "",//"ENGESV"
    }
  ]
});

function addPlaylistToAmplitude(bible, audio_bible, show_playlist = true) {
  let playlist = []
  let playlistHTML = '<h2>' + bible.title + '</h2>'
  if(show_playlist) {
    document.getElementById('bible_list').classList.add('hidden')
    document.getElementById('chapter_list').classList.remove('hidden')
  }

  let audio_paths = groupBy(bible.audio_paths, 'order')

  for (let book_order in audio_paths) {
    let book_group = audio_paths[book_order]

    playlistHTML += '<button class="accordion">' + book_group[0].name + '</button><div class="panel">'

    for (let chapter_i = 0; chapter_i < book_group.length; chapter_i++) {
      let path = book_group[chapter_i]

      playlist.push({
        "name": path.book_id + ' ' + path.chapter_id,
        "artist": audio_bible.title,
        "album": path.book_id,
        "chapter_id": parseInt(path.chapter_id),
        "url": 'http://cloud.faithcomesbyhearing.com/mp3audiobibles2/' + path.path,
        "cover_art_url": '/assets/img/books/' + (path.order - 1) + '_' + path.id_usfm + '.jpg'
      })

      //'<span class="amplitude-play" data-amplitude-song-index="1" data-amplitude-playlist="test_playlist"></span>'
      playlistHTML += '<div class="amplitude-play chapter" ' +
          'data-amplitude-playlist="' + bible.id + '"' +
          'data-amplitude-song-index="' + (playlist.length - 1) + '">' + path.chapter_id + '</div>'
    }

    playlistHTML += '</div>'
  }

  Amplitude.addPlaylist(bible.id, {'title': bible.title}, playlist)
  document.getElementById('chapter_list').innerHTML = (show_playlist) ? playlistHTML : ''
  Amplitude.bindNewElements()
}

axios.get('audio-bibles.json').then(function (audio_bibles) {

  for (let i = 0; i < audio_bibles.length; i++) {
    let audio_bible = audio_bibles[i]

    if(!process.env.MIX_LIMITED_LANGUAGES.split(",").includes(audio_bible.iso)) {
      continue;
    }

    let title_vernacular = (audio_bible.title_vernacular != undefined) ? audio_bible.title_vernacular : ''
    let title = (audio_bible.title != undefined) ? audio_bible.title : ''
    let date = (audio_bible.date != undefined) ? audio_bible.date : ''

    // Default Bible
    if(audio_bible.id === "ENGESV") {
      axios.get('audio-bibles/ENGESV.json').then(function(bible) {
        addPlaylistToAmplitude(bible, audio_bible, false);
        Amplitude.playPlaylistSongAtIndex(0, "ENGESV")
        Amplitude.pause();
      })
    }

    let audio_bible_el = document.createElement('div')
    audio_bible_el.id = audio_bible.id
    audio_bible_el.addEventListener("click", function (event) {
      axios.get('audio-bibles/' + this.id + '.json').then(function(bible) {
        addPlaylistToAmplitude(bible, audio_bible);
        accordion();
      })
    })
    audio_bible_el.innerHTML = '<li>' +
        '<div class="title_vernacular">' + title_vernacular + '</div>' +
        '<div class="title">' + title + '</div>' +
    '</li>'

    document.getElementById('bible_list_wrap').appendChild(audio_bible_el)
  }

  new List('bible_list', {
    valueNames: [ 'title', 'title_vernacular' ]
  });
})


let groupBy = function(xs, key) {
  return xs.reduce(function(rv, x) {
    (rv[x[key]] = rv[x[key]] || []).push(x);
    return rv;
  }, {});
};

//

document.getElementById('prev_album').addEventListener('click', jumpToAlbum)
document.getElementById('next_album').addEventListener('click', jumpToAlbum)

function jumpToAlbum() {
  console.log('Current Playlist:')
  let playlist = Amplitude.getActivePlaylistMetadata()
  // amplitude-play-pause
  // amplitude-paused
  for (let i = playlist.active_index; i < playlist.songs.length && i > -1;(this.id === 'next_album') ? i++ : i--) {
    if(playlist.songs[i].chapter_id === 1 && i !== playlist.active_index) {
      Amplitude.pause()
      Amplitude.playPlaylistSongAtIndex(i, Amplitude.getActivePlaylist())
      document.getElementsByClassName('amplitude-play-pause')[0]
          .classList.replace('amplitude-paused','amplitude-playing')
      break;
    }
  }
}
